import { Component, Prop, Event, EventEmitter, State, h, Host } from '@stencil/core';
import { LogoType } from '../bh-logo-box/bh-logo-box';

export interface MenuItem {
  label: string;
  icon: string;
  isActive?: boolean;
}

export interface FooterItem {
  label: string;
  icon: string;
}

export interface DropdownMenuItem {
  id: string;
  label?: string;
  icon?: string;
  shortcut?: string;
  divider?: boolean;
}

@Component({
  tag: 'bh-sidebar',
  styleUrl: 'bh-sidebar.css',
  shadow: true,
})
export class BhSidebar {
  /**
   * Type of logo to display
   */
  @Prop() logoType: LogoType = 'actabl';

  /**
   * Avatar image URL
   */
  @Prop() avatarUrl?: string;

  /**
   * Avatar initials (used if avatarUrl is not provided)
   */
  @Prop() avatarInitials: string = 'DM';

  /**
   * Avatar name for dropdown
   */
  @Prop() avatarName: string = 'Daniel Mobara';

  /**
   * Avatar email for dropdown
   */
  @Prop() avatarEmail: string = 'daniel@solsticehospitality.com';

  /**
   * Main menu navigation items
   */
  @Prop() menuItems: MenuItem[] = [
    { label: 'Dashboard', icon: 'dashboard', isActive: true },
    { label: 'Analytics', icon: 'analytics', isActive: false },
    { label: 'Reports', icon: 'assessment', isActive: false },
    { label: 'Calendar', icon: 'calendar_today', isActive: false },
    { label: 'Messages', icon: 'message', isActive: false },
    { label: 'Contacts', icon: 'contacts', isActive: false },
    { label: 'Files', icon: 'folder', isActive: false },
  ];

  /**
   * Footer navigation items
   */
  @Prop() footerItems: FooterItem[] = [
    { label: 'Help', icon: 'help_outline' },
    { label: 'Settings', icon: 'settings' },
  ];

  /**
   * Whether sidebar is collapsed
   */
  @Prop() collapsed: boolean = false;

  /**
   * Event emitted when a menu item is clicked
   */
  @Event() bhMenuItemClick!: EventEmitter<string>;

  /**
   * Event emitted when search button is clicked
   */
  @Event() bhSearchClick!: EventEmitter<void>;

  /**
   * Event emitted when avatar menu item is clicked
   */
  @Event() bhAvatarMenuClick!: EventEmitter<DropdownMenuItem>;

  @State() activeItem: string = '';

  componentWillLoad() {
    const activeMenuItem = this.menuItems.find(item => item.isActive);
    this.activeItem = activeMenuItem?.label || '';
  }

  private handleNavItemClick = (label: string) => {
    this.activeItem = label;
    this.bhMenuItemClick.emit(label);
  };

  private handleSearchClick = () => {
    this.bhSearchClick.emit();
  };

  private handleAvatarMenuClick = (event: CustomEvent<DropdownMenuItem>) => {
    this.bhAvatarMenuClick.emit(event.detail);
  };

  render() {
    const sidebarClasses = {
      'sidebar-container': true,
      'sidebar-collapsed': this.collapsed,
    };

    const avatarMenuItems: DropdownMenuItem[] = [
      { id: '1', label: 'View profile', icon: 'person', shortcut: '⌘P' },
      { id: '2', label: 'Account settings', icon: 'settings', shortcut: '⌘,' },
      { id: '3', label: 'Notifications', icon: 'notifications', shortcut: '⌘N' },
      { id: '4', label: 'Privacy & Security', icon: 'shield', shortcut: '⌘S' },
      { id: '5', divider: true },
      { id: '6', label: 'Billing', icon: 'credit_card', shortcut: '⌘B' },
      { id: '7', label: 'Team management', icon: 'group', shortcut: '⌘T' },
      { id: '8', label: 'Integrations', icon: 'extension', shortcut: '⌘I' },
      { id: '9', divider: true },
      { id: '10', label: 'Help center', icon: 'help', shortcut: '⌘H' },
      { id: '11', label: 'Keyboard shortcuts', icon: 'keyboard', shortcut: '?' },
      { id: '12', divider: true },
      { id: '13', label: 'Sign out', icon: 'logout', shortcut: '⇧⌘Q' },
    ];

    return (
      <Host>
        <div class={sidebarClasses}>
          <div class="navigation">
            {/* Gradient bar */}
            <div class="gradient-bar">
              <div class="detail-bar"></div>
            </div>

            {/* Main content */}
            <div class="sidebar-content">
              {/* Logo box */}
              <bh-logo-box logoType={this.logoType}></bh-logo-box>

              {/* Navigation section */}
              <div class="navigation-section">
                {/* Search */}
                <div class="search-container">
                  <bh-button-icon
                    hierarchy="secondary"
                    size="sm"
                    iconName="search"
                    onClick={this.handleSearchClick}
                  ></bh-button-icon>
                </div>

                {/* Nav items */}
                <div class="nav-items">
                  {this.menuItems.map((item) => (
                    <bh-nav-item
                      key={item.label}
                      icon={item.icon}
                      label={item.label}
                      isActive={this.activeItem === item.label}
                      onBhClick={() => this.handleNavItemClick(item.label)}
                    ></bh-nav-item>
                  ))}
                </div>
              </div>

              {/* Footer */}
              <div class="sidebar-footer">
                <div class="footer-navigation">
                  {this.footerItems.map((item) => (
                    <bh-nav-item
                      key={item.label}
                      icon={item.icon}
                      label={item.label}
                      isActive={false}
                      onBhClick={() => this.handleNavItemClick(item.label)}
                    ></bh-nav-item>
                  ))}
                </div>

                {/* Avatar Dropdown */}
                <bh-dropdown
                  variant="avatar"
                  size="md"
                  avatarSize="md"
                  avatarSrc={this.avatarUrl}
                  avatarName={this.avatarName}
                  avatarEmail={this.avatarEmail}
                  menuItems={avatarMenuItems}
                  showIcons={true}
                  onBhItemClick={this.handleAvatarMenuClick}
                ></bh-dropdown>
              </div>
            </div>
          </div>
        </div>
      </Host>
    );
  }
}
